<template>
<div class="college-full-screen college Mixeddataifon1">
    <div class="college_content">

        <div class="Curriculum">
            <div class="Curriculum_img">
                <div class="Curriculum_back" @click="goback">
                    <i style="color:#b0f9e0" class="el-icon-back"></i>
                    <span>返回上级</span>
                </div>
                <h1>{{obj.courseName}}分析</h1>
            </div>
            <div class="Curriculum-dv">
                <h2>{{obj.courseName}}分析</h2>
                <ul>
                    <li>上课学期：{{obj.term}}</li>
                    <li>课程类型：{{obj.className}}</li>
                    <li>考核方式及比例：{{obj.examine}}</li>
                    <li>学时：{{obj.period}}</li>
                    <li>学分：{{obj.credit}}</li>
                    <li>任课老师：{{obj.leaderName}}</li>
                </ul>
            </div>
            <div class="Curriculum-mu">
                <div class="g_box">
                    <div class="g_title">课程目标</div>
                    <ul class="g_ul">
                        <li class="g_li" v-for="(item ,index) in goalList" :key="index">
                            <div class="g_li-lf">{{index + 1}}</div>
                            <div class="g_li-rt">
                                <p>{{item.name}}</p>
                                <span>{{item.content}}</span>
                                <ul>
                                    <li >
                                        {{item.point}}
                                    </li>
                                </ul>
                            </div>
                        </li>
                    </ul> 
                </div>
            </div>
            <!-- <div class="Curriculum-ke">
                <div class="Curriculum-ke-list" v-for="(item,index) in listkecheng" :key="index"  @click="tabactive(index)">
                    <span :class="{isactive:active == index}">{{item.title}}</span>
                </div>
            </div> -->
            <div class="Curriculum-mu">
                <div class="g_box">
                    <div class="g_title">支撑指标点</div>
                        <ul class="g_ul">
                            <li class="g_li" v-for="(item ,index) in pointList" :key="index">
                                <div class="g_li-lf">{{item.pointCode}}</div>
                                <div class="g_li-rt">
                                    <p style="font-weight:bold;">指标点{{item.pointCode}}</p>
                                    <span>{{item.pointContent}}</span>
                                    <!-- <ul>
                                        <li v-for="(items, index) in item.courseNames" :key="index">
                                        {{items}}
                                        </li>
                                    </ul> -->
                                </div>
                            </li>
                        </ul>   
                    </div>
            </div>
            <div class="Curriculum_search">
                <div class="Curriculum_search_lf">
                    <span class="cl-btn">课程目标达成评价</span>
                </div>
                <div class="Curriculum_sear_rt">
                    <!-- <div class="Curriculum_search_rt">
                        <el-select v-model="page.grade" filterable placeholder="请选择">
                        <el-option
                            v-for="item in options"
                            :key="item.id"
                            :label="item.name"
                            :value="item.id"
                        ></el-option>
                        </el-select>
                    </div> -->
                </div>
            </div>
            <!-- 按照年级  查询柱状图缺接口 -->
            <div class="Curriculum_echarts">
                <echarts5 id="sbilityQueryBar" :series-bar-data="seriesBarData" :isMax="true" :isMin="true"></echarts5>
            </div>
            <div class="Curriculum_search">
                <div class="Curriculum_search_lf">
                    <span class="cl-btn">课程目标达成评价报告</span>
                </div>
                <div class="Curriculum_sear_rt">
                    <!-- <div class="Curriculum_search_rt">
                        <el-select v-model="page.grade" filterable placeholder="请选择">
                        <el-option
                            v-for="item in options"
                            :key="item.id"
                            :label="item.name"
                            :value="item.id"
                        ></el-option>
                        </el-select>
                    </div> -->
                </div>
            </div>
            <div class="Curriculum_word">
                <div class="Curriculum_sp" v-for="(item,index) in wordlist" :key="index" @click="xiazai(item)">
                    <svgIcon icon-class="iconkechengdagang-word" style="margin-left: 10px;font-size: 50px;"></svgIcon>
                    <span>{{item.className + item.teacherName}}</span>
                </div>
            </div>
        </div>
    </div>
</div>
</template>

<script>
import Self from "@/api/self";
import echarts5 from "@/components/echaars/echars5"
export default {
    name: 'CollegeCourse',
    components: {echarts5},
    data() {
        return {
            // screenWidth: document.body.clientWidth,     // 屏幕宽
            // screeHeight: document.body.clientHeight,    // 屏幕高
            active:0,
            obj:{},
            goalList:[],
            seriesBarData: {},
            page:{
                grade: '',
            },
            options:[
                {
                    name:'2015级',
                    id:'1'
                },
                {
                    name:'2016级',
                    id:'2'
                }
            ],
            pointList: [],
            wordlist:[],
            pointListsifon:[],
            name: ''
        }
    },
    created() {
        
    },
    mounted() {
        this.ifondata()
    },
    methods: {
        tabactive(index){
            this.active = index
        },
        goback(){
            this.$router.go(-1)
        },
        ifondata(){
            // let data = {
            //     title : '毕业要求1：工程知识达成情况分析',
            //     analysis : {
            //         name:'hfsdbfdjs ',
            //         value:["0.804", "0.832", "0.764", "0.780"]
            //     },
            //     target : {
            //         name:'2016',
            //         value:  ["0.650", "0.650", "0.650", "0.650"]
            //     },
            //     indevaval : {
            //         name: "间接达成值",
            //         value:["0.0", "0.0", "0.0", "0.0"]
            //     },
            //     value1: ["指标点1", "指标点2", "指标点3", "指标点4"]
            // }
            // /basic/specialty/portrayal/courseAnalyze?courseId=628195890938511360
             this.seriesBarData={}
            this.$get(`/basic/specialty/portrayal/courseAnalyze?courseId=${this.$route.query.id}`).then(res=>{
                if(res.status == 1){
                    this.obj = res.data.course
                    this.goalList = res.data.goalList
                    this.pointList = res.data.pointGoals
                    // this.pointListsifon = res.data.goalratioMark
                    let arr = []
                    let arr1 =[]
                    let arr2 = []
                    res.data.goalratioMark.forEach((item, index) => {
                        arr1.push(item.name)
                        if(index === 0) {
                        item.marks.forEach((item1, index1) => {
                            arr2.push(String(item1.grade) + '-'+ Number(item1.grade + 1)+ '学年')
                            arr.push({
                            grade: item1.grade,
                            children: [item1.goalmark]
                            })
                        })
                        } else {
                        item.marks.forEach((item1, index1) => {
                            arr.forEach((item2, index2) => {
                            if(item1.grade === item2.grade) {
                                item2.children.push(item1.goalmark)
                            }
                            })
                        })
                        }
                    })
                    const top = arr2;
                    const tip = arr1
                    const echartData = {
                        legendData: top,
                        xAxisData: tip,
                        seriesData: top.map(item => {
                            return {
                                name: item,
                                barGap: "0", //柱状图间距
                                data: []
                            };
                        })
                    };
                    arr.forEach((ele,index) => {
                        echartData.seriesData[index].data = ele.children
                    });
                    this.seriesBarData = echartData;
                    this.wordlist = res.data.reportList
                    if(res.fileId == null){
                        this.fileId = res.fileId
                    }else{
                        this.fileId = res.fileId
                    }
                }
            })
            
        },
        xiazai(item) {
            window.location.href = `${
                this.API_URL
            }/teacher/courseGoalExport/portrayal/getCourseDetail?classId=${item.classId}&courseId=${this.$route.query.id}&teacherId=${item.teacherId}`;
        },
    }
}
</script>
<style lang="scss">

.Mixeddataifon1 {
    overflow-y: scroll !important;
}

.Curriculum {
    .Curriculum_img {
        box-sizing: border-box;
        height: 85px;
        background: url("../../assets/img/bg-OBE.png") no-repeat;
        background-size: 100% 100%;
        position: relative;
        .Curriculum_back{
            display: inline-block;
            width:120px;
            height:28px;
            border:1px solid rgba(221,221,221,1);
            border-radius:14px;
            text-align: center;
            margin-top: 27px;
            margin-left: 30px;
            cursor: pointer;
            span{
                font-size:14px;
                font-family:PingFang SC;
                font-weight:500;
                color:rgba(255,255,255,1);
                
            }
        }
        h1{
            font-size:24px;
            font-family:PingFang SC;
            font-weight:bold;
            color:rgba(255,255,255,1);
            text-align: center;
            position: absolute;
            top: 32%;
            left: 50%;
            transform: translateX(-50%);
        }
    }

    .Curriculum-dv {
        background-color: #fff;
        margin-top: 30px;
        padding: 30px;
        h2 {
            font-size: 20px;
            font-family: PingFang SC;
            font-weight: bold;
            color: rgba(50, 50, 50, 1);
        }

        ul {
            overflow: hidden;
            display: flex;
            justify-content: space-between;
            li {
                margin-top: 40px;
                // margin-right: 135px;
                list-style: none;
                font-size: 14px;
                font-family: PingFang SC;
                font-weight: 400;
                color: rgba(50, 50, 50, 1);
            }
            li:last-of-type{
                margin-right: 0;
            }
        }
    }
    .Curriculum-mu{
        background-color: #fff;
        margin-top: 30px;
        padding: 30px;
        padding-bottom: 0;
        h2{
            font-size:20px;
            font-family:PingFang SC;
            font-weight:bold;
            color:rgba(50,50,50,1);
            margin-bottom: 40px;
        }
        .Curriculum_bto1{
            display: inline-block;
            width: 50%;
            margin-bottom: 20px;
            position: relative;
            span{
                display: block;
                font-size:14px;
                font-family:PingFang SC;
                font-weight:bold;
                color:#323232;
            }
            span:nth-child(4){
                color: #323232;
            }
            img{
                float: left;
                margin-left: 30px;
                margin-right: 30px;
            }
            p{
                position: absolute;
                top: 12px;
                left: 47px;
            }
        }
    }
    .Curriculum-ke{
        margin-top: 30px;
        overflow: hidden;
        .Curriculum-ke-list{
            float: left;
            margin-right: 40px;
            width:200px;
            height:40px;
            text-align: center;
            line-height: 40px;
            span{
                font-size:14px;
                font-family:PingFang SC;
                font-weight:500;
                color:rgba(150,150,150,1);
                text-align: center;
                display: block;
            }
            .isactive{
                width:200px;
                height:40px;
                background:rgba(3,177,135,1);
                box-shadow:0px 2px 20px 0px rgba(3,177,135,0.3);
                border-radius:20px;
                color: #fff;
            }
        }
    }
    .Curriculum_search{
        margin:30px 30px 30px 0px;
        .Curriculum_search_lf{
            display:inline-block;
        }
        .Curriculum_sear_rt{
            float: right;
            display: flex;
            .Curriculum_search_rt{
                margin-left: 20px;
            }
        }
    }
    .Curriculum_echarts{
        background-color: #fff;
        padding-top: 30px;
    }
    .g_box{
        min-height:136px;
        background:#fff;
        border-radius:10px;
        margin-bottom:20px;
        padding:20px;
        padding-bottom: 0;
        padding: 0;
        .g_title{
            line-height:20px;
            font-size:20px;
            font-weight:bold;
            color:#323232;
        }
        .graduation-ul{
            padding-top: 27px;
            
            ul{
                width:100%;
                li{
                    list-style: none;
                    float: left;
                    width: 29%;
                    padding-bottom: 20px;
                    font-size:14px;
                    color:#323232;
                }
            }
        }
        .g_ul{
            padding:20px 0;
            padding-bottom: 0;
            padding-left: 20px;
            display: flex;
            flex-wrap: wrap;
            width: 100%;
            .g_li{
                min-height:92px;
                width: 48%;
                min-width: 48%;
                text-align:left;
                display:flex;
                justify-content: space-between;
                padding-bottom: 20px;
                margin-right: 2%;
                .g_li-lf{
                    height: 49px;
                    width: 49px;
                    border: 1px solid #cccccc;
                    border-radius: 50%;
                    position: relative;
                    display: -webkit-box;
                    display: -ms-flexbox;
                    display: flex;
                    -webkit-box-pack: center;
                    -ms-flex-pack: center;
                    justify-content: center;
                    -webkit-box-align: center;
                    -ms-flex-align: center;
                    align-items: center;
                    color: #696969;
                    font-size: 16px;
                }
                .g_li-lf:after{
                    content:'';
                    width:14px;
                    height:14px;
                    background:rgba(3,177,135,1);
                    border-radius:50%;
                    position:absolute;
                    left:0;
                    top:0;
                }
                .g_li-rt{
                    width:calc(100% - 80px);
                    p{
                        font-size:14px;
                        color:#323232;
                    }
                    span{
                        display: inline-block;
                        font-size:14px;
                        color:#323232;
                        margin:10px 0;
                    }
                    ul{
                        li{
                            padding:3px 8px;
                            border:1px solid rgba(221,221,221,1);
                            border-radius:5px;
                            font-size:14px;
                            color:#969696;
                            float:left;
                            margin-right:20px;
                            margin-bottom:10px;
                            &:hover{
                                color:#03B187;
                                font-weight:bold;
                                background:rgba(3,177,135,0.1);
                                border-color:rgba(3,177,135,1);

                            }
                        }
                    }
                }
            }
        }
        .graduation-ke{
            display:inline-block;
            margin: 20px 39px;
            cursor:pointer;
            span{
                display: block;
                font-size:14px;
                color:#323232;
                margin-left: -30px;
                margin-top:10px;
            }
        }
    }
    .Curriculum_word{
        background-color: #fff;
        margin-bottom: 100px;
        overflow: hidden;
        padding: 30px;
        .Curriculum_sp{
            float: left;
            margin-right: 1vw;
            margin-top: 30px;
            margin-left: 1vw;
            span{
                width: 72px;
                font-size:12px;
                font-family:PingFang SC;
                font-weight:400;
                color:rgba(50,50,50,1);
                display: block;
                text-align: justify;
            }
        }
        // .Curriculum_sp:nth-child(14n){
        //     margin-right: 0;
        // }
    }
}
@import "../../styles/college.scss";
</style>



